/*global chrome*/
import React, { Component } from "react"
import axios from "axios"

import storage from "storage.js"
import Drawer from "@material-ui/core/Drawer"
import CommentList from "./CommentList"
import Button from "@material-ui/core/Button"
import TextField from "@material-ui/core/TextField"
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles"
import { videoDanmuUrl } from "config/urls"
import "./CommentDrawer.css"

function getVideoId() {
  if (window.location.href.indexOf("localhost:3210") >= 0) return "xO8Cz-9qKTI"

  const urlParams = new URLSearchParams(window.location.search)
  return urlParams.get("v")
}
function secToTimestamp(totalSeconds) {
  let hours = Math.floor(totalSeconds / 3600)
  totalSeconds %= 3600
  let minutes = Math.floor(totalSeconds / 60)
  let seconds = totalSeconds % 60

  // If you want strings with leading zeroes:
  minutes = String(minutes).padStart(2, "0")
  hours = String(hours).padStart(2, "0")
  seconds = String(seconds).padStart(2, "0")
  return hours + ":" + minutes + ":" + seconds
}
const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#2196F3"
    },
    secondary: {
      main: "#b5ff01"
    }
  }
})

class YoutubeCommentDrawer extends Component {
  state = {
    showDrawer: false,
    commentList: [],
    danmuInput: "",
    currentTime: 0,
    submitting: false,
    submitted: null,
    login: null // todo: rename to account
  }
  commentDict = {}
  lastCheckedSec = -1
  videoId = null
  videoPlayer = null
  showDanmu = true // if false then won't get new comments
  tabVisible = true

  constructor(props) {
    super(props)
    console.log("load Youtube comment drawer + button")
  }
  componentDidMount() {
    document.addEventListener(
      "visibilitychange",
      this.handleTabVisibilityChange,
      false
    )

    storage.get("account", account => {
      this.setState({ login: account })
      storage.get("videoDanmuEnabled", showDanmu => {
        if (showDanmu != null) {
          this.showDanmu = showDanmu
        }
        // wait a sec for setting this.state.login
        setTimeout(this.checkVideoChanged, 1 * 1000)
      })
    })
    storage.addEventListener("account", account => {
      this.setState({ login: account })
    })
    storage.addEventListener("videoDanmuEnabled", val => {
      //TODO: not tested yet
      this.showDanmu = val
    })
    // chrome.storage.onChanged.addListener((changes, area) => {
    //   if ("videoDanmuEnabled" in changes) {
    //     const newVal = changes["videoDanmuEnabled"]["newValue"]
    //     console.log("videoDanmuEnabled changed " + newVal)
    //     this.showDanmu = newVal
    //   }
    // })
  }

  showDrawer = () => {
    this.setState({
      showDrawer: true
    })
  }
  onClose = () => {
    this.setState({
      showDrawer: false
    })
  }
  handleDanmuInputChange = event => {
    this.setState({ danmuInput: event.target.value })
  }
  clearDanmuInput = () => {
    this.setState({ danmuInput: "" })
  }
  postComment = () => {
    this.setState({ submitting: true })
    const sec = this.state.currentTime
    const content = this.state.danmuInput
    let payload = {
      videoId: this.videoId,
      type: "Youtube",
      content: content,
      sec: sec
    }
    const url = `${videoDanmuUrl}/api/v1/video_danmu/${this.videoId}`

    axios
      .post(url, payload, { headers: { token: this.state.login.token } })
      .then(res => {
        this.setState({
          danmuInput: "",
          submitted: true
        })

        const danmu = {
          sec: sec,
          key: secToTimestamp(sec) + content + this.state.login.name,
          // id: secToTimestamp(sec), //temp solution
          timestamp: secToTimestamp(sec),
          content: content,
          user: this.state.login
        }
        if (sec in this.commentDict) {
          this.commentDict[sec].push(danmu)
        } else {
          this.commentDict[sec] = [danmu]
        }
        this.setCommentListFromDict()
        window.queueCanvasDanmu(danmu)
      })
      .catch(error => {
        console.error(error)
        this.setState({
          submitted: false
        })
      })
      .then(() => {
        this.setState({ submitting: false })
        setTimeout(() => {
          this.setState({ submitted: null })
        }, 3500)
      })
  }
  setCommentListFromDict = () => {
    let commentList = []
    Object.keys(this.commentDict).forEach(sec => {
      this.commentDict[sec].forEach(c => {
        commentList.push(c)
      })
    })
    this.setState({ commentList: commentList })
  }
  getYoutubeComments = () => {
    if (!(this.showDanmu && this.tabVisible)) return
    let url = `${videoDanmuUrl}/api/v1/video_danmus/${
      this.videoId
    }?type=youtube`
    console.log("fetch Youtube comments")

    axios
      .get(url, {
        headers: { token: this.state.login ? this.state.login.token : null }
      })
      .then(res => {
        const commentDict = res.data.comments
        Object.keys(commentDict).forEach(sec => {
          commentDict[sec].forEach(c => {
            c.sec = sec
            c.timestamp = secToTimestamp(sec)
            c.key = c.timestamp + "-" + c.name + "-" + c.content
          })
        })
        this.commentDict = commentDict
        this.setCommentListFromDict()
        // Get token from local storage, send it to server
        // when getting comments, let server tell whether login or not
        if (!res.data.login) {
          this.setState({ login: null })
        }

        if (window.location.href.indexOf("localhost") >= 0) {
          this.state.commentList.forEach(c => {
            // window.queueCanvasDanmu(c);
          })
        }
      })
  }
  videoSeekTo = sec => {
    if (!this.videoPlayer) {
      console.error("videoPlayer is null")
      return
    }
    window.clearVideoDanmu()
    this.videoPlayer.currentTime = sec
  }
  registerVideoEvents = () => {
    if (!this.videoPlayer) {
      console.error("no video player")
      return
    }
    this.videoPlayer.addEventListener("timeupdate", e => {
      const sec = Math.floor(e.target.currentTime)
      if (sec === this.lastCheckedSec) return // avoid same sec trigger multiple times
      this.lastCheckedSec = sec
      this.setState({ currentTime: sec })
      if (this.tabVisible && sec in this.commentDict) {
        this.commentDict[sec].forEach(function(comment) {
          window.queueCanvasDanmu(comment)
        })
      }
    })
    this.videoPlayer.addEventListener("pause", e => {
      window.setVideoState(false)
    })
    this.videoPlayer.addEventListener("play", e => {
      window.setVideoState(true)
    })
    // this.videoPlayer.addEventListener('seeked', (e) => {
    //   // it's better to clear danmo before seeked, otherwise won't see
    //   // danmu at seeking timestamp
    //   window.clearVideoDanmu(true);
    // });
  }
  checkVideoChanged = () => {
    const newVideoId = getVideoId()
    if (newVideoId !== this.videoId) {
      console.log("video changed from " + this.videoId + " to " + newVideoId)
      this.videoId = newVideoId
      window.clearVideoDanmu()
      this.commentDict = {}
      this.setState({ commentList: [] })
      if (this.videoId) {
        this.getYoutubeComments()
        if (!this.videoPlayer) {
          this.videoPlayer = document.querySelector("video")
          this.registerVideoEvents()
        }
      }
    }
    let danmuBar = document.getElementById("sp-danmu-bar")
    if (danmuBar && this.videoId) {
      danmuBar.style.display = "block"
      if (danmuBar.parentElement.className.indexOf("ytd-watch-flexy") === -1) {
        // console.log("Youtube danmu bar not at correct position yet")
        let infoWrapperQuery = document.querySelectorAll(
          "#info.ytd-watch-flexy"
        )
        if (infoWrapperQuery.length) {
          let infoWrapper = infoWrapperQuery[0]
          console.log("Found Youtube info element, move danmu bar")
          infoWrapper.insertBefore(danmuBar, infoWrapper.firstChild)
        }
      }
    } else {
      if (window.location.href.indexOf("localhost:3210") === -1) {
        danmuBar.style.display = "none"
      }
    }
    setTimeout(this.checkVideoChanged, 3 * 1000)
  }
  handleTabVisibilityChange = () => {
    this.tabVisible = !document["hidden"]
  }

  render() {
    let danmuInputButtons = <span />
    if (this.state.danmuInput) {
      if (this.state.login) {
        let submitBtnText = "Submit"
        if (this.state.submitting) submitBtnText = "Submitting..."
        danmuInputButtons = (
          <span className="sp-danmu-input-btn-group">
            <Button
              style={{ fontSize: "1.2rem" }}
              variant="contained"
              onClick={this.clearDanmuInput}
              className="sp-button"
            >
              Cancel
            </Button>
            <Button
              style={{ fontSize: "1.2rem" }}
              disabled={this.state.submitting}
              variant="contained"
              color="primary"
              onClick={this.postComment}
              className="sp-button"
            >
              {submitBtnText}
            </Button>
          </span>
        )
      } else {
        danmuInputButtons = (
          <span
            onClick={window.openChatbox}
            className="sp-danmu-input-not-login"
          >
            Please login first.
          </span>
        )
      }
    }
    if (this.state.submitted != null) {
      let submitStatus = "Submitted!"
      let submitStatusClass = "sp-danmu-input-sent"
      if (this.state.submitted) {
        submitStatusClass += " success"
      } else {
        submitStatusClass += " fail"
        submitStatus = "Error"
      }
      danmuInputButtons = (
        <span className={submitStatusClass}>{submitStatus}</span>
      )
    }
    return (
      <MuiThemeProvider theme={theme}>
        <div id="sp-danmu-bar" className="sp-youtube-bar">
          <Button
            color="primary"
            style={{ verticalAlign: "top", fontSize: "1.2rem" }}
            variant="contained"
            onClick={this.showDrawer}
            className="sp-button"
          >
            弹幕{" "}
            <span className="sp-comment-count">
              {this.state.commentList.length}
            </span>
          </Button>
          <span id="sp-danmu-input">
            <span className="sp-danmu-input-timestamp">
              {secToTimestamp(this.state.currentTime)}
            </span>
            <TextField
              placeholder="Enter comment here..."
              multiline
              InputProps={{
                classes: {
                  input: "sp-danmu-input-textarea"
                }
              }}
              style={{ width: "300px", marginTop: "10px" }}
              value={this.state.danmuInput}
              onChange={this.handleDanmuInputChange}
            />
            {danmuInputButtons}
          </span>
          <Drawer
            ModalProps={{
              BackdropProps: {
                classes: {
                  root: "sp-drawer-mask"
                }
              }
            }}
            style={{ zIndex: 2147483646 }}
            anchor="right"
            open={this.state.showDrawer}
            onClose={this.onClose}
          >
            <div
              className="sp-no-outline"
              tabIndex={0}
              role="button"
              onKeyDown={this.onClose}
            >
              <CommentList
                comments={this.state.commentList}
                closeDrawer={this.onClose}
                commentClickHandler={this.videoSeekTo}
              />
            </div>
          </Drawer>
        </div>
      </MuiThemeProvider>
    )
  }
}

export default YoutubeCommentDrawer
