/*global chrome*/

import React, { Component } from "react"
import YoutubeCommentDrawer from "../VideoDanmu/YoutubeCommentDrawer"
import DuonaoCommentDrawer from "../VideoDanmu/DuonaoCommentDrawer"
import Canvas from "../VideoDanmu/Canvas"
// AnimationDanmu is a confusing name, it means realtime chat danmu
// powered by css + js, different from video danmu that's powered by canvas
import AnimationDanmu from "../ChatDanmu/AnimationDanmu"
import ChatboxIframe from "../ChatboxIframe"

// const danmuUrl = 'http://localhost:9000/db/video_danmu';
// TODO: danmuUrl is only here because we haven't update Duonao
// to use the Python API
const danmuUrl = "https://api.yiyechat.com/db/video_danmu"

class App extends Component {
  render() {
    let commentDrawer = <span />
    if (
      window.location.href.indexOf("youtube") >= 0 ||
      window.location.href.indexOf("localhost:3210") >= 0
    ) {
      commentDrawer = <YoutubeCommentDrawer />
    }
    if (window.location.href.indexOf("dnvod") >= 0) {
      commentDrawer = <DuonaoCommentDrawer danmuUrl={danmuUrl} />
    }

    let canvas = <span />
    if (
      window.location.href.indexOf("youtube") >= 0 ||
      window.location.href.indexOf("dnvod") >= 0 ||
      window.location.href.indexOf("localhost") >= 0
    ) {
      canvas = <Canvas />
    }
    return (
      <span>
        {commentDrawer}
        {canvas}
        <ChatboxIframe />
        <AnimationDanmu />
      </span>
    )
  }
}

export default App
