/*global chrome*/
import React, { Component } from "react"
import Button from "@material-ui/core/Button"
// import { Resizable, ResizableBox } from "react-resizable"
import Resizable from "re-resizable"

import samePageIcon from "icon.png"
import "./ChatboxIframe.css"
import storage from "storage.js"
import {
  useLocalIframe,
  iframeDefaultDisplay,
  showIframeControl,
  iframeSize
} from "config/iframe"
import { extensionIframeUrl, localIframeUrl } from "config/urls"
const FOR_SAME_PAGE_EXTENSION = true
const CREATE_IFRAME_BY_DEFAULT = FOR_SAME_PAGE_EXTENSION

class ChatboxIframe extends Component {
  state = {
    // default state must be false, because we don't
    // want the render function to create iframe before reading
    // the config from local storage
    createChatboxIframe: false,
    minimize: false,
    display: iframeDefaultDisplay
  }
  locationHref = window.location.href
  constructor(props) {
    super(props)
    this.iframeRef = React.createRef()
    window.openChatbox = this.showHideIframe
  }
  componentDidMount() {
    this.keepCheckingLocation()
    window.addEventListener(
      "message",
      e => {
        if (!e || !e.data) return
        if (e.data === "minimize") {
          this.setState({
            display: "none",
            minimize: true
          })
        }
      },
      false
    )
    // Two options related to this:
    // 1. showChatbox
    // 2. showDanmu
    // If neither is set, refer to CREATE_IFRAME_BY_DEFAULT
    // and chatbox shouldn't display
    // If either of them is true, create iframe
    storage.get("showChatboxByDefault", showChatboxByDefault => {
      if (showChatboxByDefault) {
        this.setState({
          createChatboxIframe: true,
          display: "block"
        })
      } else {
        storage.get("realtimeDanmuEnabled", realtimeDanmuEnabled => {
          if (realtimeDanmuEnabled == null) {
            // realtimeDanmuEnabled is true by default and we want to
            // create hidden iframe for danmu to work
            this.setState({ createChatboxIframe: CREATE_IFRAME_BY_DEFAULT })
          } else {
            this.setState({
              createChatboxIframe: realtimeDanmuEnabled
            })
          }
        })
      }
    })
    if (chrome.extension) {
      chrome.runtime.sendMessage({ clearIcon: true })
      document.addEventListener(
        "visibilitychange",
        this.handleTabVisibilityChange,
        false
      )

      chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
        if (!request.chatboxMsg) return
        var msg = request.chatboxMsg
        console.debug(msg)
        if (msg === "open_chatbox") {
          this.showHideIframe()
        }
        if (msg === "is_chatbox_open") {
          sendResponse({ is_chatbox_open: this.isChatboxOpen() })
        }
      })
    }
  }
  showHideIframe = () => {
    // if iframe not created, create iframe (default display)
    // if iframe is created, toggle show hide
    if (this.state.createChatboxIframe) {
      console.log(this.iframeRef.current.style)
      if (this.state.display === "none") {
        this.setState({ display: "block" })
      } else {
        this.setState({ display: "none" })
      }
    } else {
      this.setState({
        createChatboxIframe: true,
        display: "block"
      })
    }
  }

  keepCheckingLocation = () => {
    // always post title and url to iframe because
    // the iframe may not listen and get the first event..
    // todo: improve this
    this.locationHref = window.location.href
    var msg = {
      locationUpdate: true,
      title: document.title,
      url: window.location.href
    }
    if (this.iframeRef.current) {
      this.iframeRef.current.contentWindow.postMessage(msg, "*")
    }
    setTimeout(() => {
      this.keepCheckingLocation()
    }, 3000)
  }

  // moveFrame = left => {
  //   let chatboxLeft = left
  //   if (chatboxLeft < 0) chatboxLeft = 0
  //   let maxLeft = window.innerWidth - 200
  //   if (chatboxLeft > maxLeft) chatboxLeft = maxLeft
  //   this.iframeRef.current.style.left = chatboxLeft + "px"
  // }

  isChatboxOpen = () => {
    return this.state.createChatboxIframe && this.state.display == "block"
  }

  render() {
    let chatboxIframe = ""
    let iframeControl = ""
    let restoreIframe = ""
    if (this.state.createChatboxIframe) {
      let iframeSrc = localIframeUrl
      if (chrome.extension && !useLocalIframe) {
        iframeSrc = chrome.extension.getURL(extensionIframeUrl)
      }
      console.log("render chatbox iframe!")
      chatboxIframe = (
        <Resizable
          style={{ display: this.state.display }}
          className="sp-chatbox-iframe-wrapper"
          defaultSize={{
            width: iframeSize.width,
            height: iframeSize.height
          }}
          minHeight={iframeSize.minHeight}
          minWidth={iframeSize.minWidth}
        >
          <iframe
            ref={this.iframeRef}
            className="sp-chatbox-iframe"
            src={iframeSrc + "?" + this.locationHref}
          />
        </Resizable>
      )
      if (showIframeControl) {
        iframeControl = (
          <div>
            <Button
              color="primary"
              variant="contained"
              onClick={() => {
                this.showHideIframe()
              }}
            >
              Toggle chatbox
            </Button>
          </div>
        )
      }
      if (this.state.minimize) {
        restoreIframe = (
          <img
            title="显示聊天盒"
            onClick={() => {
              this.setState({
                display: "block",
                minimize: false
              })
            }}
            src={samePageIcon}
            style={{
              zIndex: 2147483647,
              margin: 5,
              width: 32,
              height: 32,
              cursor: "pointer",
              position: "fixed",
              top: "45%",
              color: "#2096f3"
            }}
          />
        )
      }
    }
    return (
      <div className="sp-iframe-div">
        {iframeControl}
        {restoreIframe}
        {chatboxIframe}
      </div>
    )
  }
}

export default ChatboxIframe
