/*global chrome*/
import React, { Component } from "react"
import Danmu from "./Danmu"
import storage from "storage.js"
import "./AnimationDanmu.css"
// import { localIframeUrl } from "config/urls"

let invitationStr = " invites you to "
let lng = window.navigator.userLanguage || window.navigator.language
if (lng.indexOf("zh") > -1) {
  invitationStr = " 邀请你去 "
}
class AnimationDanmu extends Component {
  ROW_NUM = 10
  danmuId = 0 // unique identifier of a danmu, increment locally
  state = {
    danmuList: []
  }
  danmuWaitList = []

  constructor(props) {
    super(props)
    this.realtimeDanmuWrapperRef = React.createRef()
    window.queueAnimationDanmu = this.queueDanmu
    window.addFakeDanmu = this.addFakeDanmu
  }

  addDanmu = danmu => {
    this.setState((state, props) => {
      state.danmuList.push(danmu)
      return { danmuList: state.danmuList }
    })
  }

  addFakeDanmu = () => {
    let danmu = {
      // content: "hello world, this is cool! <span class='inline-emoji'>😙</span><span class='inline-emoji'>😌</span><span class='inline-emoji'>😞</span><span class='inline-emoji'>😒</span><span class='inline-emoji'>😪</span><span class='inline-emoji'>😭</span>",
      content: "hello world, this is cool! ",
      // me: true,
      profileImgSrc:
        "https://dnsofx4sf31ab.cloudfront.net/600f90bc-95d1-7bec-13c4-85b6a79a6b6e.jpg"
    }
    this.queueDanmu(danmu)
  }

  removeDanmu = id => {
    this.setState((state, props) => {
      let newList = state.danmuList.filter(danmu => {
        return danmu.id !== id
      })
      return { danmuList: newList }
    })
  }

  createDanmuObj = msg => {
    console.log(msg)
    let danmu = {
      id: this.danmuId++,
      type: msg.type,
      content: msg.content,
      avatarSrc: msg.avatarSrc,
      row: 1,
      self: msg.self
    }
    // avatar
    // if (danmu.avatarSrc === "profile-empty.png") {
    //   if (chrome.extension) {
    //     danmu.avatarSrc = chrome.extension.getURL(
    //       "chatbox-only/profile-empty.png"
    //     )
    //   }
    // }
    // if image body
    if (msg.type === "sticker") {
      danmu.img = true
      if (chrome.extension) {
        danmu.imgSrc = chrome.extension.getURL("build/" + danmu.content)
      } else {
        danmu.imgSrc = danmu.content
      }
    }
    // if invitation
    // if (danmu.type === "invitation") {
    //   danmu.content =
    //     msg.username +
    //     invitationStr +
    //     ` <a target='_blank' class='invitation-url' title='${msg.url}' href='${
    //       msg.url
    //     }'>${msg.pageTitle}</a>`
    // }
    return danmu
  }
  findSpot = () => {
    let occupiedRows = {}
    const spacing = 50 // and avatar space
    this.state.danmuList.forEach(danmu => {
      let x =
        danmu.ref.danmuRef.current.getBoundingClientRect().left +
        danmu.ref.danmuRef.current.offsetWidth +
        spacing
      if (x >= window.innerWidth) {
        occupiedRows[danmu.row] = true
      }
      // TODO: can take multiple rows
    })
    for (let i = 1; i <= this.ROW_NUM; i++) {
      if (!occupiedRows[i]) return i
    }
    return null
  }
  checkDanmuWaitlist = () => {
    while (this.danmuWaitList.length) {
      let availableRow = this.findSpot()
      if (availableRow) {
        // console.log('found spot');
        let danmu = this.danmuWaitList.shift()
        danmu.row = availableRow
        danmu.top = 50 * availableRow
        this.addDanmu(danmu)
      } else {
        console.log("no spot")
        setTimeout(this.checkDanmuWaitlist, 1000)
        return
      }
    }
  }
  queueDanmu = msg => {
    // this function is called by other class
    // bind this to current class
    // console.log('realtime queue ' + msg.content);
    this.danmuWaitList.push(this.createDanmuObj(msg))
    this.checkDanmuWaitlist()
  }
  receiveMsgFromChatboxFrame = e => {
    if (!e || !e.data) return
    // e.stopPropagation(); // multiple danmu.js maybe
    var danmuMsg = e.data
    if (danmuMsg.msg) {
      console.log(danmuMsg)
      danmuMsg.msg.content = danmuMsg.msg.message
      this.queueDanmu(danmuMsg.msg)
    }
    // username: socket.username,
    // message: cleanedMsg,
    // sender: socket.userId,
    // url: socket.roomId, // why need this field?
    // hasAvatar: socket.hasAvatar
  }
  toggleDanmuVisibility = visible => {
    if (visible) {
      this.realtimeDanmuWrapperRef.current.style.display = "block"
    } else {
      this.realtimeDanmuWrapperRef.current.style.display = "none"
    }
  }
  componentDidMount() {
    window.addEventListener("message", this.receiveMsgFromChatboxFrame, false)
    if (chrome.extension) {
      storage.get("realtimeDanmuEnabled", val => {
        if (val != null) {
          this.toggleDanmuVisibility(val)
        }
      })
      chrome.storage.onChanged.addListener((changes, area) => {
        if ("realtimeDanmuEnabled" in changes) {
          const newVal = changes["realtimeDanmuEnabled"]["newValue"]
          this.toggleDanmuVisibility(newVal)
        }
      })
    }
  }
  render() {
    return (
      <div id="sp-animation-danmu" ref={this.realtimeDanmuWrapperRef}>
        {this.state.danmuList.map((danmu, index) => (
          <Danmu
            danmu={danmu}
            key={danmu.id}
            ref={_ref => {
              danmu.ref = _ref
            }}
            deleteSelf={this.removeDanmu}
          />
        ))}
      </div>
    )
  }
}

export default AnimationDanmu
